import React from 'react';
import BasePageList from '../basePage/BasePageList';
import BasePageForm from '../basePage/BasePageForm';
import MessageService from '../../services/MessageService';
import {TableData, FormPage, FormRow, BasicView, Filter} from '../../components/template/Layout';
import { ButtonSubmit, ButtonCancel, InputInGroup, Select2Field, MasterDetails } from '../../components/template/Form';
import {Redirect} from "react-router-dom";
import RestService from "../../services/RestService";
const Rest = new RestService();

const Messages = new MessageService();

class UsersList extends BasePageList
{
	static defaultProps = {
		urlBase: 'users',
		deleteUrlOverride:'user',
		title: 'menu.user.title',
		fields: [
			{
				label: 'page.user.fields.username',
				field: "username",
				width: "50%"
			},
			{
				label: 'page.user.fields.name',
				field: "nome",
				width: "50%"
			}
		]
	};

	render()
	{
		let input_fields = [{label: 'page.user.fields.username', field: "username"}];
		let filter = <Filter fields={input_fields} onChange={this.handleChange} onSubmit={this.handleOnSubmitFilter}
							 toggle="tooltip" placement="top" titletoggle="Filtrar usuários"/>;
		return (
			<TableData filter={filter} onClickPage={ this.handleClickPage } title='page.user.list.title'
					   fields={ this.props.fields } data={ this.state.itens } pagination={ this.state.pagination }
					   actions={ this.state.actions } addUrl='/users/add' titletoggle="Adicionar usuário" onEdit={ this.handleOnEditAction }
					   onDelete={ this.handleOnDeleteAction } onView={ this.handleOnViewAction }/>
		);
	}

}

/*----------------------------------------------------------------------------------------------------*/

class UsersAdd extends BasePageForm
{
	static defaultProps = {
		urlBase: 'user',
		title: Messages.getMessage('menu.user.title'),
	};

	constructor(props) {
		super(props);
		this.state = {
			fieldErrors: [],
			item: {
				id: 0,
				fieldErrors: {},
			},
			empresas: [],

		};
		this.handleChange = this.handleChange.bind(this);
		this.addEmpresa = this.addEmpresa.bind(this);
		this.deleteEmpresa = this.deleteEmpresa.bind(this);
		this.handleChangeDetails = this.handleChangeDetails.bind(this);
	}

	handleChange(e)
	{
		this.setState({[e.target.name]: e.target.value});
	}

	deleteEmpresa(index) {
		this.setState({
			empresas: this.state.empresas.filter(function (
				_,
				indexof
			) {
				return indexof !== index;
			}),
			item: {
				...this.state.item,
				fieldErrors: [],
			},
		});
	}
	addEmpresa() {
		if (
			!(
				this.state.item.empresa_id === "" ||
				this.state.item.empresa_id === undefined
			)
		) {
			this.setState({
				empresas: [
					...this.state.empresas,
					{
						empresa_id: this.state.item.empresa_id,
						empresa_nome: this.state.item.empresa_nome,
						id: this.state.item.id,
					},
				],
				item: {
					...this.state.item,
					fieldErrors: {},
					id: this.state.item.id + 1,
				},
			});
		}
	}


	handleChangeDetails(e) {
		if (
			e.target.name === "empresa_id"
		) {
			console.log(e);
			let nome = e.target.label;
			let fieldName = 'empresa_nome';
			this.setState({
				item: {
					...this.state.item,
					[e.target.name]: e.target.value,
					[fieldName]: nome,
					fieldErrors: [],
				},
			});
		}
	}
	render()
	{

		return (
			<FormPage title="page.user.add.title">
				<FormRow>
					<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
								  label='page.user.fields.name' required="required" colsize="6" />

					<InputInGroup type="email" name="email" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
								  label='page.user.fields.email' required="required" colsize="6" />
				</FormRow>
				<FormRow>
					<InputInGroup type="text" name="username" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
								  label='page.user.fields.username' required="required" colsize="6" />

					<InputInGroup type="password" name="password" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
								  label='page.user.fields.password' required="required" colsize="6" />
				</FormRow>

				<FormRow>
					<Select2Field
						name="role_id"
						colsize="4"
						onChange={ this.handleChange }
						url_view="role"
						url_list="roles"
						filterName="nome"
						displayName={['nome']}
						label="page.user.fields.role"
						required={true}
						errors={ this.state.fieldErrors }/>
				</FormRow>
				<MasterDetails
					title="page.company.fields.razao_social"
					name="companies"
					header={["Razão social"]}
					fields={["empresa_nome"]}
					items={this.state.empresas}
					onDelete={this.deleteEmpresa}
					errors={this.state.item.fieldErrors}
				>
					<FormRow>
						<Select2Field
							name="empresa_id"
							value={this.state.item.empresa_id}
							colsize="6"
							onChange={this.handleChangeDetails}
							url_view="company"
							url_list="companies"
							filterName="name"
							displayName={["name"]}
							label="page.company.fields.name"
							required={false}
							errors={this.state.fieldErrors}
						/>
						<div className="form-col-auto m-auto">
							<button
								className="btn btn-primary button-form mt-3"
								onClick={this.addEmpresa}
							>
								<i className={"fas fa-plus"} />
							</button>
						</div>
					</FormRow>
				</MasterDetails>
				<FormRow>
					<ButtonSubmit text="layout.form.sign" onClick={ this.handleOnSubmit } />
					<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
				</FormRow>
			</FormPage>
		);
	}
}

/*--------------------------------------------------------------------------------------------------*/

class UsersEdit extends BasePageForm
{
	constructor(props) {
		super(props);

		this.handleMasterDetails = this.handleMasterDetails.bind(this);
		this.handleResponse = this.handleResponse.bind(this);
		this.addEmpresa = this.addEmpresa.bind(this);
		this.deleteEmpresa = this.deleteEmpresa.bind(this);
	}
	static defaultProps = {
		urlBase: 'users',
		title: Messages.getMessage('menu.user.title')
	};

	componentDidMount(){
		if (this.props.location.state === undefined){
			this.setState(({error:true}));
			return;
		}
		let id = this.props.location.state.item_id;
		Rest.get( "user/" + id, this.state).then(this.handleResponse);
		// capturar empresas do usuario e tratar no handle
		//Rest.get().then(this.handleMasterDetails)
	}
	async handleMasterDetails(res) {
		this.setState({
			empresas: res.data.itens,
		});
	}
	handleResponse(data) {
		this.setState(
			data.data
		);
	}

	async handleOnSubmit(e) {
		Rest.put(this.props.urlBase + "/" + this.state.id, this.state).then(this.handleReceiveResponseRest)
	}

	/** MasterDetails */
	/** empresas */
	deleteEmpresa(index) {
		Rest.delete(
			"url para deletar a empresa e o id" +
			this.state.empresas[index]["empresa_id"]
		)
			.then(this.handleReceiveResponseRestDelete)
			.then((deleted) => {
				if (deleted) {
					this.setState({
						empresas: this.state.empresas.filter(function (
							_,
							indexof
						) {
							return indexof !== index;
						}),
					});
				}
			});
	}
	addEmpresa() {
		// obj pro add
		var empresa = {
			empresa_id: this.state.item.empresa_id,
			user_id: this.state.id,
		};
		Rest.post("url para o add ", empresa)
			.then(this.handleReceiveResponseRestDelete)
			.then((added) => {
				if (added) {
					Rest.get(
						"url para get com filtro de user &user_id=" +
						this.state.id
					).then((res) => {
						this.setState({
							empresas: res.data.itens
						});
					});
				}
			});
	}

	render()
	{

		let master_permission = false;
		if (this.props.role === "1"){
			master_permission = true;
		}

		return (
			this.state.error ?
				( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
				<FormPage title="page.user.edit.title">
					<FormRow>
						<InputInGroup type="text" name="nome" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
									  label='page.user.fields.name' required="required" colsize="6" value={ this.state.nome || '' } />

						<InputInGroup type="email" name="email" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
									  label='page.user.fields.email' required="required" colsize="6" value={ this.state.email || '' } />
					</FormRow>
					<FormRow>
						<InputInGroup type="text" name="username" errors={ this.state.fieldErrors }  onChange={ this.handleChange }
									  label='page.user.fields.username' required="required" colsize="6" value={ this.state.username || '' } />

					</FormRow>

					{master_permission ?
						<FormRow>

							<Select2Field
								name="role_id"
								colsize="6"
								value={ this.state.role_id }
								onChange={ this.handleChange }
								url_view="role"
								url_list="roles"
								filterName="nome"
								displayName={['nome']}
								label="page.user.fields.role"
								required={true}
								errors={ this.state.fieldErrors }
							/>
							{/* <SelectField disabled={!master_permission} empty={ true } value_name="id" name="role_id" errors={ this.state.fieldErrors }  value={this.state.role_id} onChange={ this.handleChange }
						label='page.user.fields.role' required="required" colsize="6" url="roles/all" /> */}
						</FormRow>
						:''}
					<MasterDetails
						title="page.company.fields.razao_social"
						name="companies"
						header={["Razão social"]}
						fields={["empresa_nome"]}
						items={this.state.empresas}
						onDelete={this.deleteEmpresa}
						errors={this.state.item.fieldErrors}
					>
						<FormRow>
							<Select2Field
								name="empresa_id"
								value={this.state.item.empresa_id}
								colsize="6"
								onChange={this.handleChangeDetails}
								url_view="company"
								url_list="companies"
								filterName="name"
								displayName={["name"]}
								label="page.company.fields.name"
								required={false}
								errors={this.state.fieldErrors}
							/>
							<div className="form-col-auto m-auto">
								<button
									className="btn btn-primary button-form mt-3"
									onClick={this.addEmpresa}
								>
									<i className={"fas fa-plus"} />
								</button>
							</div>
						</FormRow>
					</MasterDetails>
					<FormRow>
						<ButtonSubmit text="layout.form.edit" onClick={ this.handleOnSubmit } />
						<ButtonCancel text="layout.form.cancel" onClick={ this.handleCancel } />
					</FormRow>
				</FormPage>
		);
	}
}

/*----------------------------------------------------------------------------------------------------*/

class UsersView extends BasePageForm
{

	constructor(props) {
		super(props);

		this.handleResponse = this.handleResponse.bind(this);
		this.onClickEdit = this.onClickEdit.bind(this);
	}
	onClickEdit(event) {
		console.log(event.target);
		let url = "edit";
		let id = this.state.id;
		this.props.history.push({
			pathname: url,
			state: {item_id: id}
		});
	}
	static defaultProps = {
		urlBase: 'users/view',
		title: Messages.getMessage('menu.user.title')
	};

	componentDidMount() {
		if (this.props.location.state === undefined){
			this.setState(({error:true}));
			return;
		}
		let id = this.props.location.state.item_id;
		Rest.get( "user/" + id, this.state).then(this.handleResponse);
	}

	handleResponse(data) {
		this.setState((
			data.data
		));
	}
	render()
	{
		let fields = [
			{label:"Username: ", value:this.state.username},
			{label:"Nome: ", value:this.state.nome},
			{label:"Email: ", value:this.state.email},
			{label:"Tipo de Usuário", value: (this.state.role ? this.state.role.name : '')}
		]
		return (
			this.state.error ?
				( <Redirect to={{ pathname: "/login", state: { from: this.props.location } }}/> ) :
				(<BasicView status={this.state.response_status} title={Messages.getMessage("page.user.view.title")} url={"#user/edit?id=" + this.state.id} fields={fields} onClickEdit={this.onClickEdit}/>)
		);
	}
}


export { UsersList, UsersAdd , UsersEdit,  UsersView};
